<template>
  <div class="image">
    <van-button @click="getImage" type="info">相册</van-button>
    <img :src="this.src" width="100px" height="100px" />
  </div>
</template>
<script>
export default {
  data() {
    return {
      src: "",
    };
  },
  mounted() {},
  methods: {
    getImage() {
    //   console.log("11");
    console.log(this.onPlusReady);
    //   this.onPlusReady(function () {
        console.log();
        console.log("plus",this.plus);
        this.plus.gallery.pick(
          function (e) {
            console.log("e",e);
            var name = e.substr(e.lastIndexOf("/") + 1);
            console.log("name",name);
            //    compressImage(e,name);
          },
          function (e) {
            console.log("取消选择");
          },
          {
            filter: "image",
          }
        );

    //   });
    },
  },
};
</script>

<style scoped>
.image {
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  top: 0;
}
</style>


